import React, { Component } from 'react'
import './Demo.css'

import Viz from './viz/Viz'

import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

const scrollContent = [
  {
    id: 'topic',
    text: 'In 2012, the Wisconsin Department of Corrections began using COMPAS - software that predicts a defendant\'s likelihood of reoffending - for making sentencing decisions.',
    width: 4,
    align: 'center'
  },
  {
    id: 'score-system',
    text: 'Each defendant was assigned a score from 0 to 10, with 0 being least likely to offend and 10 being most likely.',
    width: 4,
    align: 'right'
  },
  {
    id: 'distribution',
    text: 'Here is the overall distribution',
    width: 4,
    align: 'right'
  },
  {
    id: 'topic4',
    text: 'Why hello there I am Vu',
    align: 'right'
  }
]

class Demo extends Component {
  componentDidMount() {
    ScrollTrigger.create({
      trigger: '.dpd-scroll-area',
      start: 'top top',
      end: 'bottom top',
      scrub: 0.2,
      pin: '.dpd-viz-wrap'
    })

    scrollContent.forEach(section => {
      ScrollTrigger.create({
        trigger: `#dpd-scroll-section-${section.id}  > .dpd-scroll-text-wrap`,
        start: 'top bottom',
        end: 'bottom top',
        scrub: 0.2,
        animation: gsap.timeline()
          .add(gsap.to(
            `#dpd-scroll-section-${section.id} > .dpd-scroll-text-wrap`, 
            { opacity: 1, scale: 1, duration: 0.5, ease: 'power4.out' }), 0)
          .add(gsap.to(
            `#dpd-scroll-section-${section.id} > .dpd-scroll-text-wrap`, 
            { opacity: 0, scale: 0.8, duration: 0.5, ease: 'power4.in' }), 0.5)
      })
    })
  }

  render() {
    return (
      <div className="dp-demo row">
        <div className="dpd-viz-wrap">
          <Viz />
        </div>
        <div className="dpd-scroll-area">
          <div className="dpd-scroll-area-head"></div>
          <div className="dpd-scroll-area-main">
            {scrollContent.map((section, index) => {
              return (
                <div 
                  id={`dpd-scroll-section-${section.id}`}
                  className={`dpd-scroll-section ${section.align}`} 
                  key={section.id}
                >
                  <div className={`dpd-scroll-text-wrap col-${section.width}`}>
                    <p className="dpd-scroll-text">{section.text}</p>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </div>
    )
  }
}

export default Demo
