import React, { Component } from 'react'
import './Viz.scss'

import gsap from 'gsap'
import ScrollTrigger from 'gsap/ScrollTrigger'
import * as d3 from 'd3'

const data = {
  xScale: [0, 10],
  yScale: [0, 100]
}

const animations = [
  {
    id: 'score-system',
    animations: [
      {
        target: '',
        from: { opacity: 0 },
        to: { opacity: 1 },
        location: 'start'
      },
      {
        target: '.x-axis > .domain',
        from: { drawSVG: '0%' },
        to: { drawSVG: '100%' },
        location: 'start'
      },
      {
        target: '.x-axis > .tick > text',
        from: { opacity: 0 },
        to: { opacity: 1 },
        location: 'start',
        stagger: true
      },
      {
        target: '.x-axis > .tick > line',
        from: { drawSVG: '0%' },
        to: { drawSVG: '100%' },
        location: 'start',
        stagger: true
      }
    ]
  },
  {
    id: 'distribution',
    animations: [
      {
        target: '.y-axis > .tick > text',
        from: { opacity: 0 },
        to: { opacity: 1 },
        location: 'start',
        stagger: true
      },
      {
        target: '.y-axis > .tick > line',
        from: { drawSVG: '0%' },
        to: { drawSVG: '100%' },
        location: 'start',
        stagger: true
      }
    ]
  }
]

const colors = {
  dark: '#fff6e0'
}

class Viz extends Component {
  constructor(props) {
    super(props)
    this.createViz = this.createViz.bind(this)
    this.bindAnimations = this.bindAnimations.bind(this)
  }

  createViz() {
    const width = 600, height = 400
    const svg = d3.select('.dpd-viz')
      .append('svg')
        .attr('preserveAspectRatio', 'xMidYMid meet')
        .attr('viewBox', `0 0 ${width} ${height}`)
        .attr('width', width)
        .attr('height', height)
        .attr('class', 'dpd-svg')


    // X-AXIS
    const xScale = d3.scaleLinear()
      .domain(data.xScale)
      .range([0, width * 0.85])

    const xAxis = d3.axisBottom(xScale)
      .ticks((data.xScale[1] - data.xScale[0]) / 2)
      .tickSizeInner(-height)
      .tickSizeOuter(0)

    svg.append('g')
      .attr('class', 'x-axis')
      .attr('transform', `translate(${width * 0.075} ${height * 0.925})`)
      .call(xAxis)

    // Y-AXIS
    const yScale = d3.scaleLinear()
      .domain(data.yScale)
      .range([height * 0.85, 0])

    const yAxis = d3.axisLeft(yScale)
      .ticks(4)
      .tickSizeInner(-width)
      .tickSizeOuter(0)

    svg.append('g')
      .attr('class', 'y-axis')
      .attr('transform', `translate(0 ${height * 0.075})`)
      .call(yAxis)
  }

  bindAnimations() {
    animations.forEach(section => {
      const timeline = gsap.timeline()

      section.animations.forEach(animation => {
        const toObject = Object.assign({
          ease: 'expo.out',
          duration: 0.5,
          stagger: animation.stagger ? 0.02 : 0
        }, animation.to)
        const insertionTime = animation.location === 'start' ? 0 : 0.5

        if (animation.from) {
          timeline.add(gsap.fromTo(`.dpd-svg ${animation.target}`, animation.from, toObject), insertionTime)
        } else {
          timeline.add(gsap.to(`.dpd-svg ${animation.target}`, toObject), insertionTime)
        }
      })

      timeline.set({}, {}, 1);

      ScrollTrigger.create({
        trigger: `#dpd-scroll-section-${section.id}  > .dpd-scroll-text-wrap`,
        start: 'top bottom',
        end: 'bottom top',
        scrub: 0.2,
        animation: timeline
      })
    })
  }

  componentDidMount() {
    this.createViz()
    setTimeout(() => this.bindAnimations(), 200)
  }

  render() {
    return (
      <div className="dpd-viz flex-center col-8">
      </div>
    )
  }
}

export default Viz
